import { ff } from "./filter-form";
var select = {
	selector: ".js-select-box",
	header: ".js-select-header",
	name: ".js-select-name",
	close: ".js-close",
	list: ".js-list",
	count: ".js-selected-count",
	reset: ".js-list-reset",
	resetAll: ".js-reset-all",
	selected: ".js-selected",
	activeClass: "is-active",

	template: {
		list: (name, title, params) => {
			let inputs = "";

			params.forEach((input) => {
				inputs += `<label class="select-checkbox__label p">
						<input class="select-checkbox__input" data-title="${input.name}" name="${name}[]" type="checkbox"><span class="select-checkbox__row"><span class="select-checkbox__status"><svg class="icon icon-checkbox" viewBox="0 0 12 10"><use xlink:href="/app/icons/sprite.svg#checkbox"></use></svg></span>
						<span class="select-checkbox__text">${input.name}</span><span class="select-checkbox__count">(${input.count})</span></span>
					</label>`;
			});

			return `<div class="select-checkbox__back middle b js-close">
						<svg class="icon icon-arrow4" viewBox="0 0 17 9">
							<use xlink:href="/app/icons/sprite.svg#arrow4"></use>
						</svg>${title}</div>
					<div class="select-checkbox__list-header">
						<div class="select-checkbox__list-top flex flex_justify">
							<div class="select-checkbox__list-count p js-selected-count">Выбрано 0</div>
							<div class="select-checkbox__list-reset p js-list-reset">Очистить</div>
						</div>
						<div class="select-checkbox__selected select-checkbox__selected_desktop p js-selected"></div>
					</div>
					<div class="select-checkbox__inputs">${inputs}</div>
					<div class="select-checkbox__apply btn btn_small js-close"><span class="btn__back js-circles"></span><span class="btn__text">Применить</span>
					</div>`;
		},
	},

	method: {
		close: () => {
			$(select.selector).removeClass(select.activeClass);
		},

		reset: (el) => {
			let $inputs = $(el).find("input:checked");

			$inputs.prop("checked", false).trigger("change");
			//
		},

		append: (el) => {
			if ($(el).hasClass("is-loaded") || !$(el).attr("data-params"))
				return false;

			let params = $(el).data("params");

			let $list = $(el).find(select.list);

			let title = $.trim($(el).find(select.name).text());

			let name = $(el).data("name");

			$(el).addClass("is-loaded");

			$list.append(select.template.list(name, title, params));
		},

		toggle: (evt, el) => {
			if ($(evt.target).hasClass(select.reset.replace(".", ""))) return;

			console.log("te");

			select.method.append(el.closest(select.selector));
			$(el).toggleClass(select.activeClass);
		},

		change: (el) => {
			let $inputs = $(el).find("input:checked");
			let $selected = $(el).find(select.selected);
			let $name = $(el).find(select.name);
			let $count = $(el).find(select.count);

			let values = $inputs
				.map(function () {
					return $(this).data("title");
				})
				.get();

			if ($inputs.length) {
				$(el).addClass("is-checked");
				$selected.addClass("is-visible");
				$name.attr("data-selected", $inputs.length);
			} else {
				$(el).removeClass("is-checked");
				$selected.removeClass("is-visible");
				$name.removeAttr("data-selected");
			}

			$count.text(`Выбрано ${$inputs.length}`);

			$selected.text(values.join(" / "));
		},
	},

	run: (el) => {
		$(el)
			.find(select.header)
			.on("click", (e) => select.method.toggle(e, el));

		$(document).on("mouseup", (e) => {
			let container = $(el);
			if (
				!container.is(e.target) &&
				container.has(e.target).length === 0 &&
				container.hasClass(select.activeClass)
			) {
				select.method.close();
			}
		});
	},

	init: () => {
		if (!$(select.selector).length) return false;

		$(select.selector).each((i, el) => select.run(el));

		$(document).on("change", `${select.list} :input`, (e) => {
			let parent = e.currentTarget.closest(select.selector);
			select.method.change(parent);
		});

		$(document).on("click", select.close, select.method.close);
		$(document).on("click", select.reset, (e) => {
			select.method.reset(e.currentTarget.closest(select.selector));
		});

		$(select.resetAll).on("click", (e) => {
			select.method.reset(e.currentTarget.closest(ff.selector));
		});
	},
};

export { select };
