// import Swiper from "swiper";

(() => {
	let $catalog = $("#catalog-list");

	if (!$catalog.length) return false;

	const sliderInit = () => {
		const $sliders = $('.catalog__slider:not(.swiper-container-initialized');

		$sliders.each(function (i) {
			$(this).append('<div class="catalog__slider-nav"><button class="catalog__slider-nav-btn btn-back" tabindex="0" role="button" aria-label="Previous slide"><svg class="catalog__default" width="17" height="9" viewBox="0 0 17 9" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1.7649 4.57892L5.17231 7.98633M1.7649 4.57892L5.17231 1.17151M1.7649 4.57892L15.3945 4.57892" stroke="#000AFF" stroke-width="1.5" stroke-linecap="round"></path></svg></button><button class="catalog__slider-nav-btn btn-next" tabindex="0" role="button" aria-label="Next slide"><svg class="catalog__default" width="16" height="9" viewBox="0 0 16 9" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M14.8054 4.57928L11.398 1.17188M14.8054 4.57928L11.398 7.98669M14.8054 4.57928H1.17578" stroke="#000AFF" stroke-width="1.5" stroke-linecap="round"></path></svg></button></div>');

			const $prev = $(this).find('.btn-back');
			const $next = $(this).find('.btn-next');

			window[`productSlider${i}`] = new Swiper(
				$(this),
				{
					loop: true,
					spaceBetween: 20,
					slidesPerView: 1,
					keyboardControl: true,
					grabCursor: true,

					navigation: {
						prevEl: $prev,
						nextEl: $next,
					},
					// pagination: {
					// 	el: ".catalog__dots",
					// 	clickable: true,
					// },
				}
			);
		});

		// var productslider = new Swiper(
		// 	".catalog__slider:not(.swiper-container-initialized)",
		// 	{
		// 		loop: true,
		// 		spaceBetween: 20,
		// 		slidesPerView: 1,
		// 		keyboardControl: true,
		// 		grabCursor: true,
		//
		// 		navigation: {
		// 			prevEl: $(this).find('.btn-back'),
		// 			nextEl: $(this).find('.btn-next'),
		// 		},
		// 		// pagination: {
		// 		// 	el: ".catalog__dots",
		// 		// 	clickable: true,
		// 		// },
		// 	}
		// );
	};

	sliderInit();

	let catalog__slider = document.querySelectorAll('.catalog__slider');

	$(document).on("click", ".js-case-type", (e) => {
		let $catalog = $("#catalog-list");
		let $this = $(e.currentTarget);
		let current_type = $this.attr("data-type");
		let $old = $(".js-case-type.is-active");
		let old_type = $old.attr("data-type");

		$old.removeClass("is-active");
		$this.addClass("is-active");


		if ($this.attr("data-inline")) {
			$catalog.removeClass("is-table");
			$catalog.addClass("is-inline");
		} else if ($this.attr("data-table")) {
			$catalog.removeClass("is-inline");
			$catalog.addClass("is-table");
		} else if (current_type == "case") {
			$catalog.removeClass("is-inline");
			$catalog.removeClass("is-table");
		}

		catalog__slider.forEach(function(item, i, arr) {
			item.swiper.update();
		});

		/*for (var i = catalog__slider.length - 1; i >= 0; i--) {
			catalog__slider[i].swiper.update();
		}*/

		/*if (old_type != current_type) {
			$.ajax({
				type: "GET",
				url: `/app/api/catalog_${current_type}.html`,
				success: (data) => {
					$catalog.empty().append(data);

					if (current_type == "table") {
						// tables.build();
						mytab.init();
						$(".catalog__nav-bottom").hide();
					}
					if (current_type == "case") {
						sliderInit();
						$(".catalog__nav-bottom").show();
					}
				},
			});
		}*/
	});

	document.addEventListener("catalogUpdate", function(event) {
		sliderInit();
	});

	$(".js-select-type").click(function(){

		var typeIndex = $(".js-select-type .catalog__nav-type.is-active").index()+1;
		var typeLength = $(".js-select-type .catalog__nav-type").length;

		if ( typeIndex == typeLength ) {
			$(".js-select-type .catalog__nav-type.is-active").removeClass("is-active");
			$(".js-select-type .catalog__nav-type:first-child").addClass('is-active');
			$(".js-case-type:first-child").click();
		} else {
			$(".js-select-type .catalog__nav-type.is-active")
				.removeClass("is-active")
				.next()
				.addClass("is-active");
			$(".js-case-type:last-child").click();
		}

	});

	$(".js-show-filter").click(function(){

		$(".catalog__cell_left").addClass('is-visible');
		$('body').addClass('js-lock');

	});

	$(".js-close-sidebar").click(function(){

		$('body').removeClass('js-lock');
		$(".catalog__cell_left").removeClass('is-visible');

	});
})($);
