import { thousandSeparator } from "../functions";

(() => {
	if (!$(".js-slider-ui").length) return false;

	$(".js-slider-ui").each(function () {
		let slider = $(this).closest(".price");
		let $range = $(this);
		let $priceFields = $(slider).find(".js-price-from, .js-price-to");
		var price_from = parseInt(
			$(slider).find(".js-price-from").attr("data-value")
		);
		var price_to = parseInt(
			$(slider).find(".js-price-to").attr("data-value")
		);

		var min = parseInt($range.attr("data-min"));
		var max = parseInt($range.attr("data-max"));

		console.log(price_from, price_to, min, max);

		$priceFields.on("change", function () {
			let $from = $(slider).find(".js-price-from");
			let $to = $(slider).find(".js-price-to");

			let from_val = parseInt($from.val().replace(/ /g, ""));
			let to_val = parseInt($to.val().replace(/ /g, ""));

			if ($(this).hasClass("js-price-from")) {
				if (from_val > to_val) {
					$to.val(thousandSeparator(from_val));
					to_val = from_val;
				}
			} else {
				if (to_val < from_val) {
					$from.val(thousandSeparator(to_val));
					from_val = to_val;
				}
			}

			$range.slider("values", [from_val, to_val]);
		});

		$range.slider({
			range: true,
			min: min,
			max: max,
			values: [price_from, price_to],
			start: () => {
				$priceFields.css("color", "#000000");
			},

			slide: function (event, ui) {
				$(".js-price-from")
					.attr("data-value", ui.values[0])
					.val(thousandSeparator(ui.values[0]));
				$(".js-price-to")
					.attr("data-value", ui.values[1])
					.val(thousandSeparator(ui.values[1]));
			},

			stop: () => {
				$priceFields.css("color", "");
			},
		});

		// $(".js-price-from, .js-price-to").blur(function () {
		// 	var price_from = $(".js-price-from").attr("data-value");
		// 	var price_to = $(".js-price-to").attr("data-value");

		// 	var this_val = $(this).attr("data-value");
		// 	$(".js-slider-ui").slider({
		// 		values: [price_from, price_to],
		// 	});
		// });
	});
})($);
