var ff = {
	selector: ".js-filter-form",
	openButton: ".js-filter-open",
	closeButton: ".js-filter-close",
	child: ".js-filter-child",
	header: ".select-checkbox__header",
	name: ".select-checkbox__name",
	hiddenClass: "is-hidden",
	toggle: ".js-filter-toggle",
	nav: ".filter-form__column_nav",

	method: {
		toggle: (el) => {
			let content = $(el).data("text");

			$(el).toggleClass("is-active");

			if ($(el).hasClass("is-active")) {
				$(el).find(".js-text").text(content[1]);
			} else {
				$(el).find(".js-text").text(content[0]);
			}

			$(`${ff.child}.${ff.hiddenClass}`).toggleClass("is-active");
			$(ff.selector).toggleClass("is-more-open");
		},
		open: ($clickedEl) => {
			const $mainParent = $clickedEl.closest('.fullpage__part');
			const dataContent = $clickedEl.attr('data-content');
			const $form = $(`${ff.selector}[data-content=${dataContent}`);
			if ($(window).width() <= 768) $("html, body").addClass("js-lock");
			$mainParent.addClass('topmost');
			$form.addClass("is-open");
		},
		close: ($clickedEl) => {
			const $mainParent = $clickedEl.closest('.fullpage__part');
			const dataContent = $clickedEl.attr('data-content');
			const $form = $(`${ff.selector}[data-content=${dataContent}`);
			$mainParent.removeClass('topmost');
			$form.removeClass("is-open");
			$("html, body").removeClass("js-lock");
		},
	},

	init: () => {
		if (!$(ff.selector).length) return false;

		$(ff.openButton).on("click", function () {
			ff.method.open($(this));
		});

		$(ff.closeButton).on("click", function () {
			ff.method.close($(this));
		});

		$(ff.toggle).on("click", (e) => ff.method.toggle(e.currentTarget));
	},
};

export { ff };
