require("../vendors/sortElements");

var mytab = {
	selector: ".js-mytab",
	buildClass: "is-load",
	resort: ".js-sort",

	run: (el) => {
		// sorting
		$(el)
			.addClass(mytab.buildClass)
			.find(mytab.resort)
			.each(function () {
				var th = $(this),
					thIndex = th.index(),
					other = $(el).find(mytab.resort),
					inverse = false;

				th.click(function () {
					other.removeClass("is-active is-reverse");

					th.addClass("is-active");

					$(el)
						.find("td")
						.filter(function () {
							return $(this).index() === thIndex;
						})
						.sortElements(
							function (a, b) {
								return $.text([a]) > $.text([b])
									? inverse
										? -1
										: 1
									: inverse
									? 1
									: -1;
							},
							function () {
								// parentNode is the element we want to move
								return this.parentNode;
							}
						);

					inverse = !inverse;

					if (inverse) {
						th.addClass("is-reverse");
					}
				});
			});
	},

	init: () => {
		if (!$(mytab.selector).length) return false;

		$(mytab.selector)
			.not(`.${mytab.buildClass}`)
			.each((i, el) => mytab.run(el));
	},
};

export { mytab };
