var subcat = {
	selector: ".js-subcat-list",
	toggle: ".js-subcat-toggle",
	hiddenClass: "is-hidden",

	toggleMethod: (el) => {
		let $parent = $(el).closest(subcat.selector);
		$parent.toggleClass("is-open");
	},

	resize: () => {
		$(subcat.selector).removeClass("is-open is-smart");
	},

	init: () => {
		if (!$(subcat.selector).length) return false;

		$(subcat.toggle).on("click", (e) =>
			subcat.toggleMethod(e.currentTarget)
		);
	},
};

export { subcat };
